.company-desc {
    padding: 0 0 56px;
    position: relative;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url(../img/company-desc__bg.png);
        background-repeat: no-repeat;
        background-size: cover;
        pointer-events: none;
    }
    &__inner {
        display: flex;
    }
    &__left {
        position: relative;
        z-index: 2;
        width: 1070px;
    }
    &__right {
        width: calc(100% - 1070px);
    }
    &__desc {
        background: #FFFFFF;
        padding: 40px 40px 30px;
        margin-top: 35px;
        p {
            font-size: 18px;
            line-height: 24px;
            color: $gray;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        ul {
            margin-bottom: 20px;
            li {
                font-size: 18px;
                line-height: 24px;
                color: $gray;
            }
        }
    }
    &__img {
        position: relative;
        margin-top: 0;
        img {
            position: absolute;
            top: 0;
            right: 0;
        }
    }
}

.competencies {
    padding: 50px 0 60px;
    overflow: hidden;
    &__swiper {}
    &__inner {
        display: flex;
        align-items: center;
    }
    &__left {
        position: relative;
        z-index: 3;
        width: calc(100% - 48.7% - 60px);
    }
    &__right {
        width: 48.7%;
        margin-left: 60px;
        position: relative;
        padding: 50px 0 60px 30px;
        &::after,
        &:before{
            content: '';
            position: absolute;
            top: 0;
            width: 5000px;
            height: 100%;
            pointer-events: none;
        }
        &:after{
            background-color: #FFFFFF;
            left: 0;
        }
        &:before{
            background-color: $pageBg;
            right: 100%;
            z-index: 2;
        }

    }
    &__slide {
        position: relative;
    }
    &__slide-nav {
        display: flex;
        justify-content: end;
        align-items: center;
        margin-bottom: 70px;
    }
    &__button {
        &-prev {
            position: static;
            margin-top: 0;
        }
        &-next {
            position: static;
            margin-top: 0;
            margin-left: 10px;
        }
    }
    &__desc {
        margin-top: 45px;
    }
    &__slider{
        position: relative;

    }
    .swiper{
        overflow: unset;
        width: 312px;
        max-width: 100%;
        margin-left: 0;
        position: relative;
        &:before{
            z-index: 2;
            content: '';
            position: absolute;
            height: 1px;
            width: 2000px;
            top: 10px;
            left: 0;
            background: rgba(102, 105, 115, 0.14);
        }
        &-slide{
            &-active{
                .competency-card__round{
                    background: linear-gradient(96.39deg, #0D899F -11.35%, #19B8AE 114.56%);
                }
            }
        }
        &-pagination{
            display: none;
        }
    }
}

.competency-card{
    &__subtitle {
        margin-top: 25px;
    }
    &__desc {
        margin-top: 30px;
    }
    &__round{
        width: 21px;
        height: 21px;
        border-radius: 50%;
        background-color: $black;
        display: block;
        margin-bottom: 38px;
    }
}

.team {
    padding: 60px 0 90px;
    &__slide {
        position: relative;
        &-nav {
            margin-bottom: 70px;
        }
    }
    .swiper {
        width: 100%;
        height: 100%;
    }
    &__slide-nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__nav-right {
        display: flex;
        align-items: center;
    }
    &__button {
        &-next {
            position: static;
            margin-left: 10px;
            width: 52px;
            height: 52px;
        }
        &-prev {
            position: static;
            width: 52px;
            height: 52px;
        }
    }
}

.advantages {
    background: #E3E5EF;
    padding: 90px 0;
    margin: 90px 0 65px;
    &__slide {
        position: relative;
    }
    .swiper {
        width: 100%;
        height: 100%;
    }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 70px;
        &-right {
            display: flex;
            align-items: center;
        }
    }
    &__button {
        &-prev,
        &-next {
            position: static;
        }
        &-next {
            margin-left: 10px;
        }
    }
    &__card {
        background: rgba(255, 255, 255, 0.6);
        text-align: center;
        padding: 50px 15px 35px;
        height: 387px;
        display: flex;
        flex-direction: column;
        &-img {
            height: 155px;
            display: flex;
            align-items: center;
            justify-content: center;
            img{
                max-width: 100%;
                max-height: 100%;
            }
        }
        &-desc {
            font-weight: 600;
            font-size: 19px;
            line-height: 28px;
            margin-top: 34px;
        }
    }
}

.principles {
    padding: 65px 0 200px;
    &__items {
        margin-top: 55px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 38px;
    }
    &__item {
        display: grid;
        grid-template-columns: 1fr;
        gap: 45px 38px;
        justify-content: space-between;
        @media screen and (min-width: 1200px){
            &:nth-child(3n){
                .principles__item-content{
                    order: -1;
                }
            }
            &--big{
                grid-template-columns: 1fr 1fr;
                grid-column: span 2;
            }
        }
        &-img {
            img {
                width: 100%;
                height: auto;
            }
        }
        &-subtitle {
            color: #000000;
        }
        &-text {
            margin-top: 38px;
        }
    }
}


@media screen and (max-width: 1600px) {
    .company-desc {
        &__inner {
            display: block;
        }
        &__left {
            width: 100%;
        }
        &__right {
            width: 100%;
            margin-top: 50px;
        }
        &__img {
            img {
                position: static;
                width: 100%;
                height: auto;
            }
        }
    }
}


@media screen and (max-width: 1450px){
    .principles{
        &__items{
            gap: 29px;
        }
        &__item{
            gap: 29px;
        }
    }
}


@media screen and (max-width: 1200px) {
    .competencies {
        &__inner {
            display: block;
        }
        &__left {
            width: 100%;
        }
        &__right {
            width: calc(100% + 60px);
            margin: 50px 0 0 -30px;
            background-color: #fff;
            padding: 67px 30px 157px;
            &:after,
            &:before{
                display: none;
            }
        }
        &__slide{
            position: static;
            &-nav{
                margin: 0;
                position: absolute;
                bottom: 60px;
                left: 0;
                width: 100%;
                padding: 0 30px;
                justify-content: space-between;
            }
        }
        .swiper{
            &:before{
                display: none;
            }
            &-pagination{
                display: flex;
                bottom: 0;
            }
        }
    }

    .competency-card{
        &__round{
            display: none;
        }
        &__subtitle{
            margin-top: 37px;
        }
        &__desc{
            margin-top: 44px;
        }
    }

    .team{
        padding: 20px 0 45px;
    }

    .advantages{
        margin-bottom: 32px;
    }

    .principles{
        padding: 34px 0 80px;
        &__items{
            margin-top: 27px;
            grid-template-columns: 100%;
        }
        &__item{
            position: relative;
            &:nth-child(even){
                .principles__item-subtitle{
                    padding: 0 208px 0 0;
                }
                .principles__item-img{
                    left: unset;
                    right: 0;
                }
            }
            &-img{
                position: absolute;
                width: 165px;
                height: 173px;
                img{
                    height: 100%;
                    object-fit: cover;
                }
            }
            &-subtitle{
                min-height: 173px;
                display: flex;
                align-items: center;
                padding-left: 208px;
            }
            &-text{
                margin-top: 28px;
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .company-desc {
        padding: 0 0 35px;
        &__right {
            display: none;
        }
        &__desc {
            background: transparent;
            padding: 0;
            p {
                font-size: 15px;
                line-height: 20px;
            }
            ul {
                padding-left: 15px;
                li {
                    font-size: 15px;
                    line-height: 20px;
                }
            }
        }
    }

    .competencies {
        &__title {
            br {
                display: none;
            }
        }
        &__desc {
            margin-top: 30px;
        }
    }

    .advantages{
        &__card{
            padding: 37px 3px 10px;
            &-img{
                height: 105px;
            }
            &-desc{
                margin-top: 24px;
            }
        }
    }
}


@media screen and (max-width: 576px) {
    .company-desc {
        &:after {
            background-image: url("../img/company-desc__bg-mob.png");
            background-size: 100%;
        }
    }
}
