.service-page{
  background-image: url(../img/wave3.svg);
  background-position: center top;
  background-repeat: no-repeat;
}

.service-main{
  padding-bottom: 65px;
  &__inner{
    display: grid;
    grid-template-columns: 470px calc(100% - 550px);
    gap: 80px;
  }
  &__right{
    position: relative;
    padding-right: 40px;
  }
  &__title{
    margin-top: 25px;
  }
  &__desc{
    max-width: 360px;
    margin-top: 85px;

  }
  &__btn{
    width: 100%;
    margin-top: 60px;
    svg{
      margin-left: 15px;
    }
  }
  @media screen and (min-width: 1200px){
    .main__slider-pagination{
      position: absolute;
      height: 185px;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      flex-direction: column;
      justify-content: space-between;
      .swiper-pagination{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-61%,-50%) rotate(90deg);
      }
    }
  }
}

.service-slider{
  max-width: 878px;
  &__item{
    position: relative;
    padding-left: 120px;
  }
  &__content{
    max-width: 634px;
    margin-left: auto;
    background: #D4EBF1;
    padding: 53px 87px 69px;
    position: relative;
    z-index: 2;
    min-height: 100%;
  }
  &__img{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-40%);
    height: 298px;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__top{
    display: flex;
  }
  &__title{
    padding-top: 20px;
    margin-left: 15px;
  }
  &__desc{
    margin-top: 27px;
  }
  &__nav{
    padding-left: 120px;
    margin-top: 41px;
  }
  &__buttons{
    max-width: 634px;
    width: 100%;
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev,
      &-next{
        position: static;
        margin: 0 5px;
      }
    }
  }

}

.service-consult{
  padding: 60px 0;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 49% 51%;
  }
  &__desc{
    margin-top: 40px;
  }
  &__grid{
    margin-top: 70px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
  &__full{
    grid-column: 1/-1;
  }
  &__bottom{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 70px;
  }
  &__btn{
    margin-right: auto;
    padding: 0 46px;
    svg{
      margin-left: 15px;
    }
  }
  &__hint{
    margin-top: 25px;
    width: 100%;
  }
  &__content{
    position: relative;
    padding-top: 8px;
  }
  &__img1{
    position: absolute;
    top: 187px;
    left: 334px;
    height: 451px;
  }
  &__img2{
    position: absolute;
    top: 550px;
    left: 140px;
    height: 296px;
    z-index: 2;
  }
  &__info{
    max-width: 416px;
    padding: 25px 30px;
    background-color: $pageBg;
    margin-left: 197px;
    position: relative;
    z-index: 3;
  }
}


@media screen and (max-width: 1450px){
  .service-slider{
    &__title{
      padding-top: 12px;
    }
  }
}


@media screen and (max-width: 1400px){
  .service-main{
    &__inner{
      grid-template-columns: 440px calc(100% - 470px);
      gap: 30px;
    }
  }

  .service-slider{
    &__content{
      padding: 30px 45px 40px;
    }
  }
}


@media screen and (max-width: 1200px){
  .service-main{
    padding-bottom: 22px;
    &__right{
      padding: 0;
      display: flex;
      flex-direction: column-reverse;
    }
  }

  .service-slider{
    margin-top: 45px;
    &__item{
      padding: 74px 0 0;
    }
    &__img{
      top: 0;
      left: 50%;
      transform: translate(-50%,0);
      height: unset;
      max-width: 225px;
      z-index: 3;
    }
    &__nav{
      padding: 0;
      margin: 0;
    }
    &__content{
      padding: 86px 15px 75px;
    }
    .swiper{
      &-button{
        &-prev,
        &-next{
          position: absolute;
          top: 0;
        }
      }
    }
  }

  .service-consult{
    padding: 22px 0 3px;
    &__inner{
      grid-template-columns: 100%;
      position: relative;
    }
    &__img1,
    &__img2{
      display: none;
    }
    &__content{
      position: static;
    }
    &__info{
      padding: 0;
      margin: 0;
      max-width: 100%;
      position: absolute;
      top: 90px;
    }
    &__grid{
      margin-top: 110px;
      gap: 30px;
    }
    &__desc{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 992px){
  .service-main{
    &__inner{
      grid-template-columns: 100%;
      gap: 57px;
    }
    &__title{
      br{
        display: none;
      }
    }
    &__desc{
      margin-top: 24px;
      max-width: unset;
    }
    &__btn{
      margin-top: 111px;
    }
    .swiper-pagination{
      top: -192px;
      bottom: unset;
      position: absolute;
    }
  }

  .service-slider{
    max-width: unset;
    margin: 0 0 0 -15px;
    width: calc(100% + 30px);
    &__content{
      max-width: 100%;
    }
    &__title{
      margin-left: 3px;
    }
    &__desc{
      margin-top: 23px;
    }
    .swiper{
      &-button{
        &-prev,
        &-next{
          top: -192px;
        }
      }

    }
  }

  .service-consult{
    &__info{
      top: 80px;
    }
    &__bottom{
      margin-top: 40px;
    }
    &__hint{
      margin-top: 32px;
    }
    &__btn{
      padding: 0 40px;
    }
  }
}


@media screen and (max-width: 768px){
  .service-consult{
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      padding: 0;
      width: 100%;
    }
  }
}
