.error {
    padding: 0 0 220px;
    background-image: url(../img/error-bg.png);
    background-repeat: no-repeat;
    background-position: top 0 left 0;
    background-size: 100% auto;
    &__inner {
        position: relative;
    }
    &__left {
        max-width: 630px;
    }
    &__subtitle {
        margin-top: 40px;
    }
    &__items {
        margin-top: 106px;
    }
    &__item {
        display: flex;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 34px;
        }
        .social-btn{
            margin-right: 20px;
        }
    }
    &__social-network {
        margin-top: 22px;
        display: flex;
        align-items: center;
        padding-left: 71px;
    }
    &__links {
        display: flex;
        align-items: center;
        margin-left: 19px;
    }
    &__link {
        width: 52px;
        height: 52px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        &:not(:first-child) {
            margin-left: 12px;
        }
    }
    &__telegram {
        background: #50ABFF;
    }
    &__whatsapp {
        background: #4CB663;
    }
    &__404 {
        font-family: $font2;
        font-weight: 500;
        font-size: 516px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        font-feature-settings: 'ss07' on, 'ss08' on;
        background: linear-gradient(96.39deg, #0D899F -11.35%, #19B8AE 114.56%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        position: absolute;
        top: 80px;
        right: 0;
    }
}


@media screen and (max-width: 1600px) {
    .error {
        &__404 {
            font-size: 450px;
        }
    }
}

@media screen and (max-width: 1500px) {
    .error {
        &__404 {
            font-size: 350px;
        }
    }
}

@media screen and (max-width: 1300px) {
    .error {
        &__404 {
            position: static;
            font-size: 280px;
            margin-top: 20px;
        }
        &__items {
            margin-top: 50px;
        }
    }
}

@media screen and (max-width: 992px) {
    .error {
        padding: 0 0 120px;
        &__social-network {
            display: block;
        }
        &__links {
            margin-top: 20px;
        }
    }
}

@media screen and (max-width: 576px) {
    .error {
        &__404 {
            font-size: 200px;
        }
    }
}

@media screen and (max-width: 375px) {
    .error {
        &__404 {
            font-size: 180px;
        }
    }
}
