.catalog-page{
  padding-bottom: 72px;
  &__btn{
    padding: 0 33px;
    svg{
      margin-left: 16px;
    }
  }
}

.main-category{
  background-image: url("../img/wave4.svg");
  background-repeat: no-repeat;
  background-position: center bottom 54px;
  background-color: #fff;
  margin-top: -118px;
  padding: 118px 0 100px;
  &__inner{
    margin-top: 15px;
    position: relative;
  }
  &__content{
    max-width: 618px;
  }
  &__img{
    width: calc(100% - 650px);
    max-width: 679px;
    margin-left: auto;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    display: flex;
    align-items: center;
    img{
      max-height: 100%;
      width: 100%;
      display: block;
    }
  }
  &__slider{
    max-width: 700px;
    margin-left: auto;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    img{
      display: block;
      max-width: 570px;
    }
  }
  &__title{
    margin-top: 15px;
  }
  &__subtitle2{
    margin-top: 60px;
    max-width: 500px;
    text-transform: uppercase;
    color: $black;
    font-weight: 700;
  }
  &__desc{
    margin-top: 60px;
    max-width: 598px;
  }
  &__nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    margin-top: 49px;
    &--flex{
      display: flex;
      gap: unset;
      .main-category__btn{
        padding: 0 46px;
        &:not(:last-child){
          margin-right: 16px;
        }
      }
    }
  }
  &__btn{
    svg{
      margin-left: 20px;
    }
  }
}

.catalog-category{
  padding: 150px 0;
  &--pt80{
    padding-top: 80px;
  }
  .categories__grid{
    margin: 0;
  }
}

.filter{
  display: flex;
  margin-bottom: 48px;
  &__item{
    min-width: 110px;
    &:not(:last-child){
      margin-right: 70px;
    }
  }
}

.service-desc{
  padding: 80px 0;
  &--pt110{
    padding-top: 110px;
  }
  &--pt188{
    padding: 188px 0 4px;
  }
  &__title2{
    margin-bottom: 78px;
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 48.71% - 38px) 48.71%;
    gap: 38px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 2000px;
      height: 100%;
      background-color: #fff;
      right: 401px;
      top: 0;
    }
  }
  &__content{
    padding: 60px 0;
  }
  &__img,
  &__content{
    position: relative;
    z-index: 2;
  }
  &__img{
    margin-top: -78px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin-bottom: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid rgba(102, 105, 115, 0.27);
  }
  &__text{
    max-width: 754px;
  }
}

.slider-section,
.advantages{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 72px;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-pagination{
      display: none;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev,
      &-next{
        margin: 0 0 0 10px;
        position: static;
      }
    }
  }
}

.collection{
  padding: 10px 0 130px;
  .swiper-container{
    padding: 0 20px 20px 0;
  }
}

.videos{
  padding: 55px 0 110px;
}

.functionality{
  padding: 40px 0 75px;
}

.opportunity{
  padding: 75px 0;
  overflow: hidden;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left{
    width: 360px;
  }
  &__title{
    margin-top: 15px;
  }
  &__desc{
    margin-top: 85px;
  }
  &__right{
    width: calc(100% - 390px);
    max-width: 1091px;
    position: relative;
  }
  &__img{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    max-height: 92%;
    max-width: 69%;
    width: auto;
    display: block;
  }
  &__content{
    padding: 80px 50px 50px 85px;
    max-width: 635px;
    margin-left: auto;
    background-color: $blueBg;
    position: relative;
    z-index: 2;
  }
  &__desc2{
    margin-top: 28px;
  }
}

.props{
  padding: 75px 0 54px;
  &__grid{
    margin-top: 55px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    color: $gray;
  }
  &__col{
    &:nth-child(1){
      .props__item{
        padding-left: 30px;
      }
    }
  }
  &__item{
    height: 58px;
    display: flex;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding: 0 15px;
    font-size: 18px;
    line-height: 133%;
    &:nth-child(odd){
      background: #FFFFFF;
    }
  }
  &__bottom{
    margin-top: 46px;
    display: flex;
    justify-content: center;
  }
  &__link{
    padding: 0 46px;
    svg{
      margin-left: 15px;
    }
  }
}


@media screen and (max-width: 1350px){
  .filter{
    flex-wrap: wrap;
    margin-bottom: 28px;
    &__item{
      margin-bottom: 20px;
      &:not(:last-child){
        margin-right: 20px;
      }
    }
  }

  .opportunity{
    &__content{
      padding: 40px 20px;
      max-width: 65%;
    }
  }
}


@media screen and (max-width: 1200px){
  .slider-section,
  .advantages{
    .container{
      position: relative;
      padding-bottom: 77px;
    }
    &__top{
      margin-bottom: 43px;
    }
    &__nav{
      justify-content: space-between;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 15px;
      width: 100%;
    }
    .swiper{
      &-pagination{
        display: flex;
        position: static;
        width: unset;
        padding: 0;
      }
      &-button{
        &-next,
        &-prev{
          flex-shrink: 0;
          margin-left: 0;
        }
      }
    }
  }

  .main-category{
    margin-top: -99px;
    padding-bottom: 62px;
    &__content{
      max-width: 500px;
    }
    &__img,
    &__slider{
      width: calc(100% - 530px);
    }
  }

  .catalog-category{
    padding: 36px 0 73px;
  }

  .functionality{
    padding: 38px 0 45px;
  }

  .opportunity{
    padding: 35px 0;
    &__right{
      padding-top: 146px;
    }
    &__content{
      max-width: 100%;
      padding: 89px 15px 94px;
    }
    &__img{
      height: 221px;
      width: auto;
      max-width: 100%;
      max-height: unset;
      top: 0;
      left: 50%;
      transform: translate(-50%,0);
      z-index: 3;
    }
  }

  .props{
    padding: 55px 0 50px;
    &__grid{
      margin-top: 30px;
      grid-template-columns: 100%;
      gap: 58px;
    }
    &__item{
      &--mob-hide{
        display: none;
      }
      &.black-text{
        font-weight: 700;
      }
    }
    &__bottom{
      margin-top: 30px;
    }
  }

  .videos{
    padding: 55px 0;
  }

  .collection{
    padding-bottom: 65px;
  }
}


@media screen and (max-width: 992px){
  .catalog-page{
    .container{
      position: relative;
      padding-bottom: 79px;
    }
    &__btn{
      position: absolute;
      bottom: 0;
      left: 15px;
      width: calc(100% - 30px);
    }
  }

  .main-category{
    &__img{
      width: 100%;
      height: unset;
      position: static;
      margin: 42px 0 0 -15px;
      max-width: 400px;
    }
    &__content{
      max-width: 100%;
    }
    &__title{
      margin-top: 10px;
    }
    &__subtitle2{
      margin-top: 36px;
    }
    &__desc{
      margin-top: 38px;
    }
    &__nav{
      margin-top: 62px;
    }
    &__slider{
      position: relative;
      height: unset;
      margin: 32px 0 0 -15px;
      width: 100%;
      max-width: 400px;
    }
    &--item{
      .main-category__content{
        display: flex;
        flex-direction: column;
      }
      .main-category__subtitle2,
      .main-category__desc{
        order: 2;
      }
      .main-category__nav{
        margin-bottom: 174px;
        flex-wrap: wrap;
        margin-top: 60px;
      }
      .main-category__btn{
        margin-top: 18px;
        padding: 0 40px;
      }
      .main__slider-bottom{
        padding-left: 15px;
        position: absolute;
        bottom: -305px;
        width: calc(100% + 15px);
      }
      .swiper-button-prev{
        left: 15px;
      }
    }
  }

  .catalog-category{
    .category-card{
      &__desc{
        margin: 0;
        display: block;
        grid-column: 1/-1;
        padding: 0 20px 3px 0;
      }
    }
  }

  .service-desc{
    padding: 0 0 32px;
    &--pt110{
      padding-top: 50px;
      .service-desc__inner:before{
        display: none;
      }
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
      &:before{
        right: 0;
        width: 100%;
        height: 100%;
      }
    }
    &__img{
      margin: 0;
      order: -1;
    }
    &__content{
      padding: 22px 15px 33px;
    }
    &__title{
      padding-bottom: 16px;
      margin-bottom: 13px;
    }
    &__title2{
      margin-bottom: 43px;
    }
  }

  .filter{
    display: none;
  }

  .opportunity{
    &__inner{
      display: block;
    }
    &__left{
      width: 100%;
    }
    &__right{
      width: calc(100% + 30px);
      margin-left: -15px;
      margin-top: 21px;
    }
    &__title{
      margin-top: 25px;
    }
    &__desc{
      margin-top: 23px;
    }
  }

  .props{
    &__grid{
      gap: 40px;
    }
    &__item{
      min-height: 40px;
      height: unset;
      padding: 10px !important;
      font-size: 15px;
    }
  }
}


@media screen and (max-width: 768px){
  .main-category{
    &__nav{
      grid-template-columns: 100%;
      max-width: 273px;
    }
  }
}
