.use-page{
  padding-bottom: 6px;
  background-image: url(../img/wave5.svg);
  background-position: center top;
  background-repeat: no-repeat;
  &__subtitle{
    margin-top: 35px;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__link{
    padding: 0 33px;
    svg{
      margin-left: 16px;
    }
  }
  &__wrap{
    display: grid;
    grid-template-columns: 332px calc(100% - 332px);
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
    }
  }
}

.use-info{
  background-color: $blueBg;
  width: 726px;
  padding: 60px;
  position: relative;
  z-index: 2;
  &:before{
    display: none;
  }
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__img{
    width: 122px;
    margin: 0 0 -25px 25px;
  }
  &__title{
    margin-top: 15px;
  }
  &__desc{
    margin-top: 47px;
  }
}


@media screen and (max-width: 1200px){
  .use-page{
    &__wrap{
      grid-template-columns: 430px calc(100% - 430px);
    }
    &__img{
      width: calc(100% + 145px);
      margin-left: -145px;
      min-height: 421px;
      position: relative;
      img{
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  .use-info{
    width: 100%;
    margin-left: -30px;
    padding: 20px 30px 32px;
    position: relative;
    z-index: 2;
    &__img,
    &__subtitle{
      display: none;
    }
    &__title{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .use-page{
    .use__grid{
      margin-top: 31px;
    }
    .container{
      padding-bottom: 88px;
      position: relative;
    }
    &__link{
      position: absolute;
      left: 15px;
      bottom: 0;
      width: calc(100% - 30px);
      padding: 0;
    }
    &__wrap{
      grid-template-columns: 237px calc(100% - 237px);
    }
    &__img{
      width: calc(100% + 94px);
      margin-left: -94px;
      min-height: 331px;
      img{
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  .use-page2{
    .container{
      padding-bottom: 0;
    }
  }

  .use-info{
    margin-left: -15px;
    padding: 20px 15px 36px;
    &__desc{
      margin-top: 15px;
    }
  }

}
