.career {
    padding: 0 0 65px;
    background: radial-gradient(106.88% 81.19% at 108.12% 81.26%, #19B8AE 0%, rgba(25, 184, 174, 0.67) 92.62%);
    position: relative;
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url(../img/career-bg.png);
        background-repeat: no-repeat;
        background-size: cover;
        pointer-events: none;
    }
    .breadcrumbs{
        padding: 30px 0;
        position: relative;
        z-index: 2;
        &__item{
            color: #fff;
            &:before,
            &:last-child{
                color: #fff !important;
            }
        }
    }
    &__inner {
        display: flex;
        align-items: center;
        position: relative;
        z-index: 2;
    }
    &__left {
        max-width: 675px;
        margin-right: 100px;
    }
    &__img {
        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
    &__subtitle {
        font-size: 24px;
        line-height: 58px;
        letter-spacing: 0.02em;
        color: #FFFFFF;
    }
    &__title {
        margin-top: 5px;
        color: #FFFFFF;
    }
    &__text {
        margin-top: 25px;
        color: #FFFFFF;
    }
    &__btn {
        width: 258px;
        margin-top: 50px;
        display: flex;
        align-items: center;
        &-arrow {
            margin-left: 15px;
        }
    }
}

.job-openings {
    padding: 100px 0 20px;
    &__inner {
        display: flex;
    }
    &__left {
        width: calc(100% - 774px);
    }
    &__right {
        width: 678px;
        margin-left: 96px;
    }
    &__title {
        margin-bottom: 60px;
    }
    &__subtitle {
        margin-top: 20px;
        a {
            text-decoration-line: underline;
        }
    }
    &__item {
        background: #FFFFFF;
        padding: 30px 0;
        &:not(:first-child) {
            margin-top: 15px;
        }
        &-show {
            padding: 0 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            cursor: pointer;
            &--active {
                .job-openings__show-btn {
                    svg {
                        transform: rotate(180deg);
                        transition: all 0.3s;
                    }
                }
            }
        }
        &-title {
            font-weight: 700;
        }
        &-hidden {
            margin-top: 20px;
            padding: 37px 30px 0;
            display: none;
            position: relative;
            border-top: 1px solid #D6D6D9;
            &--active {
                display: block;
            }
        }
        &-desc {
            p span {
                font-weight: 700;
            }
        }
        &-info {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 50px;
        }
        &-see {
            display: flex;
            align-items: center;
            span {
                margin-right: 10px;
            }
        }
        &-btn {
            width: 258px;
            i {
                margin-left: 15px;
            }
        }
    }
    &__show-btn {
        margin-left: 10px;
    }
}

.consult {
    position: relative;
    &__bg-img {
        position: absolute;
        right: 0;
        bottom: -10px;
        pointer-events: none;
    }
}


@media screen and (max-width: 1450px) {
    .career {
        &__left {
            max-width: 550px;
            margin-right: 50px;
        }
    }

    .job-openings {
        &__right {
            width: 550px;
            margin-left: 50px;
        }

        &__left {
            width: calc(100% - 600px);
        }

    }


}

@media screen and (max-width: 1300px) {
    .job-openings {
        &__inner {
            display: block;
        }

        &__left {
            width: 100%;
        }

        &__right {
            width: 100%;
            margin-top: 90px;
            margin-left: 0;
        }

    }
}

@media screen and (max-width: 1200px) {
    .career {
        &__inner {
            display: block;
        }

        &__left {
            max-width: 100%;
            margin-right: 0;

        }

        &__right {
            margin-top: 50px;
        }
    }
}

@media screen and (max-width: 992px) {
    .career {
        &::after {
            background-image: url(../img/career-bg__mob.png);
            top: -35px;
        }


        &__subtitle {
            font-size: 20px;
        }

        &__btn {
            margin-top: 30px;
            width: 233px;
        }
    }

    .job-openings {
        &__title {
            margin-bottom: 30px;
        }
        &__item {
            &-info {
                margin-top: 20px;
                display: block;
            }
            &-btn{
                margin-top: 25px;
                width: 239px;
            }
        }
        &__mob {
            display: block;
            margin-top: 20px;
            &-title,
            &-link{
                line-height: 22px;
            }
        }
        &__subtitle {
            a {
                display: block;
                margin-top: 10px;
                text-decoration: none;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .career {
        padding: 0;
        background: none;
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: calc(100% - 200px);
            background: radial-gradient(106.88% 81.19% at 108.12% 81.26%, #19B8AE 0%, rgba(25, 184, 174, 0.67) 92.62%)
        }

    }
    .job-openings {
        padding: 60px 0 20px;
    }
}

@media screen and (max-width: 576px) {
    .career {
        &:before {
            height: calc(100% - 115px);
        }
    }
}
