.news-page{
  padding-bottom: 30px;
  background-image: url(../img/wave2.svg);
  background-position: center top;
  background-repeat: no-repeat;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 51px;
  }
  &__links{
    padding: 9px 13px;
    background: #FFFFFF;
    border-radius: 1000px;
  }
  &__link{
    padding: 0 24px;
    display: flex;
    align-items: center;
    height: 36px;
    font-weight: 700;
    font-size: 18px;
    line-height: 161%;
    &:not(:last-child){
      border-right: 1px solid #D9D9D9;
    }
    &--active{
      color: $primary;
      pointer-events: none;
    }
  }
  &__grid{
    margin-top: 40px;
  }
}

.news-filter{
  &__show{
    display: none;
  }
  &__hidden{
    display: flex;
    align-items: center;
  }
}

.search-page{
  padding-bottom: 132px;
  &__grid{
    margin-top: 73px;
  }
}

.news-item{
  padding: 0 0 18px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (min-width: 1200px){
      .breadcrumbs__back{
        display: flex;
        margin: 0;
        svg{
          display: none;
        }
      }
    }
  }
  &__date{
    font-size: 20px;
  }
  &__title{
    margin-top: 25px;
    text-transform: unset;
  }
}

.news-social{
  margin-top: 70px;
  &__items{
    margin-top: 26px;
    display: flex;
    align-items: center;
  }
  &__item{
    &:not(:last-child){
      margin-right: 30px;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.news-content{
  margin-top: 47px;
  img{
    width: 100%;
    display: block;
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
  h2{
    max-width: 1141px;
    &:not(:last-child){
      margin-bottom: 30px;
    }
  }
  .mlist{
    margin: 40px 0;
    line-height: inherit;
    padding-left: 70px;
    h5{
      margin: 0 !important;
    }
  }
}


@media screen and (max-width: 1200px){
  .search-page{
    padding-bottom: 100px;
    &__grid{
      margin-top: 43px;
    }
  }

  .news-item{
    &__top{
      flex-direction: column-reverse;
      .breadcrumbs__back{
        margin-right: auto;
        svg{
          display: unset;
        }
      }
    }
    &__date{
      width: 100%;
    }
  }

  .news-social{
    margin-top: 25px;
    &__items{
      margin-top: 5px;
    }
    &__item{
      &:not(:last-child){
        margin-right: 24px;
      }
    }
  }

  .news-content{
    margin-top: 35px;
    img{
      &:not(:last-child){
        margin-bottom: 34px;
      }
    }
    .mlist{
      padding-left: 0;
      margin: 20px 0;
    }
  }

  .news-page{
    &__top{
      margin-top: 24px;
      .arrow-link{
        display: none;
      }
    }
    &__link{
      border: none !important;
      padding: 6px 31px;
      font-size: 15px;
      line-height: 193%;
      height: unset;
    }
  }

  .news-filter{
    height: 41px;
    width: 100%;
    max-width: 500px;
    padding: 0;
    position: relative;
    &--active{
      .news-filter__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 6px 31px;
      font-weight: 700;
      font-size: 15px;
      line-height: 193%;
      color: $primary2;
      cursor: pointer;
    }
    &__hidden{
      background-color: #fff;
      padding: 6px 0;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 5px;
      display: block;
      width: 100%;
      border-radius: 20px;
      max-height: 350px;
      overflow-y: auto;
    }

  }
}
