.main-partners{
  padding-bottom: 85px;
  &--delivery{
    .main-partners__desc{
      max-width: 425px;
    }
  }
  .breadcrumbs{
    padding-bottom: 10px;
  }
  &__inner{
    display: grid;
    grid-template-columns: 540px calc(100% - 540px);
    align-items: center;
  }
  &__img{
    margin-left: -315px;
    width: calc(100% + 315px);
    position: relative;
    img{
      z-index: 2;
      position: relative;
      width: 100%;
    }
  }
  &__content{
    position: relative;
    z-index: 4;
    padding: 43px 79px 43px 0;
    margin-top: -11px;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 2000px;
      background-color: $blueBg;
      pointer-events: none;
    }
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 40px;
  }
  &__link{
    position: absolute;
    bottom: -30px;
    right: -30px;
    width: 160px;
    height: 120px;
    background-color: $blueBg;
    z-index: 3;
    &:hover{
      background-color: darken($blueBg,10%);
    }
  }
  &__rect{
    position: absolute;
    bottom: -30px;
    left: -30px;
    width: 100px;
    height: 100px;
    border: 4px solid $blueBg;
  }
}

.partners{
  padding: 85px 0 45px;
  overflow: hidden;
  &__slider{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 200px;
      width: 100vw;
      background-color: #fff;
      left: 0;
      top: 0;
      z-index: -1;
      pointer-events: none;
    }
  }
  &__img{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    height: 200px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .swiper{
    &-slide{
      width: 250px;
      padding-bottom: 300px;
      //position: static;
      &-active{
        .partners__content{
          display: flex;
        }
      }
    }
    &-button{
      &-prev,
      &-next{
        position: absolute;
      }
    }
    &-container{
      position: relative;
    }
    &-pagination{
      display: none;
    }
  }
  &__info{
    margin-top: 54px;
    position: relative;
  }
  &__content{
    position: absolute;
    width: 100vw;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    display: none;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 50px;
    &--active{
      display: flex;
    }
  }
  &__desc{
    margin-top: 60px;
    max-width: 1100px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  &__link{
    margin: 48px auto 0;
    padding: 0 46px;
    svg{
      margin-left: 16px;
    }
  }
}

.delivery-info{
  margin-top: 57px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  &__num{
    font-weight: 500;
    font-size: 90px;
    line-height: 148%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #13909A;
    font-family: $font2;
  }
  &__desc{
    margin-top: 23px;
  }
}


@media screen and (max-width: 1200px){
  .main-partners{
    padding-bottom: 19px;
    &__inner{
      grid-template-columns: 340px calc(100% - 340px);
    }
    &__content{
      padding: 20px 30px 32px 0;
    }
    &__img{
      width: calc(100% + 145px);
      margin-left: -145px;
      min-height: 421px;
      img{
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__desc{
      margin-top: 10px;
    }
    &__link{
      width: 80px;
      height: 70px;
      bottom: -32px;
      right: 14px;
      svg{
        width: 16px;
        height: auto;
      }
    }
    &__rect{
      display: none;
    }
  }

  .partners{
    padding: 19px 0 49px;
  }
}


@media screen and (max-width: 992px){
  .main-partners{
    &__inner{
      grid-template-columns: 222px calc(100% - 222px);
    }
    &__content{
      padding-right: 10px;
      margin-top: 28px;
    }
    &__img{
      min-height: 331px;
      img{
        left: 0;
      }
    }
  }

  .partners{
    position: relative;
    &__info{
      position: static;
      margin: 0;
    }
    &__desc{
      margin-top: 35px;
    }
    &__img{
      padding: 11px;
      height: 126px;
    }
    .swiper{
      &-slide{
        width: 220px;
        padding-bottom: 230px;
      }
      &-button{
        &-prev,
        &-next{
          position: static;
          margin-top: 0;

        }
      }
      &-pagination{
        bottom: 0;
        display: flex;
      }
    }
    &__nav{
      margin-top: 30px;
      position: static;
      bottom: 153px;
      left: 15px;
      width: calc(100% - 30px);
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__slider{
      &:before{
        height: 126px;
      }
    }
    &__link{
      padding: 0 34px;
      width: calc(100% - 30px);
    }
    &__content{
      width: calc(100vw - 45px);
      margin-top: 25px;
    }
  }

  .delivery-info{
    margin-top: 16px;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    &__num{
      font-size: 50px;
      line-height: 74px;
    }
    &__data{
      margin-top: 2px;
    }
    &__desc{
      margin-top: 10px;
      max-width: 320px;
    }
  }
}


@media screen and (max-width: 768px){
  .delivery-info{
    grid-template-columns: 100%;
  }
}
