.politics {
    padding: 0 0 135px;
    background-image: url(../img/error-bg.png);
    background-repeat: no-repeat;
    background-position: top 0 left 0;
    background-size: 100% auto;
    &__desc {
        padding: 50px 40px;
        margin-top: 55px;
        background: #FFFFFF;
    }
}


@media screen and (max-width: 992px) {
    .politics {
        padding: 0 0 200px;
        background-image: none;
        &__desc {
            padding: 0;
            margin-top: 45px;
            background: transparent;
        }
    }
}
